
.chat {
    flex: 0.70;
    display: flex;
    flex-direction: column;
}

.chat__header {
    padding: 20px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray;
}

.chat__headerInfo {
    flex: 1;
    padding-left: 20px;
}

.chat__headerInfo>h3 {
    margin-bottom: 3px;
    font-weight: 500;
}

.chat__headerInfo>p {
    color: gray;
}

.chat__headerRight {
    display: flex;
    justify-content: space-between;
    min-width: 100px;
}

.chat__body {
    flex: 1;
    background-image: url('https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png');
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow: auto;
}

.chat__message {
    position: relative;
    font-size: 16px;
    padding: 10px;
    padding-top: 25px;
    padding-bottom: 5px;
    background-color: white;
    border-radius: 10px;
    border-top-left-radius: 0px;
    width: fit-content;
    max-width: 70%;
    margin-bottom: 25px;
}

.chat__reciever {
    margin-left: auto;
    background-color: #DCF8C6;
    border-top-right-radius: 0px;
    border-top-left-radius: 10px;
}

.chat__name {
    position: absolute;
    top: 5px;
    font-weight: 700;
    font-size: smaller;
    white-space: nowrap;
}

.chat__mainMessage {
    word-break: break-all;
}

.chat__timestamp {
    margin-left: 10px;
    font-size: smaller;
    float: right;
}

.chat__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-top: 1px solid lightgray;
    margin-left: 10px;
    margin-right: 5px;
}

.chat__footer>form {
    flex: 1;
    display: flex;
}

.chat__footer>form>input {
    /* resize: vertical; */
    flex: 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
    outline: none;
}

.chat__footer>form>button {
    display: none;
}

.chat__footer>.MuiIconButton-root {
    padding: 10px;
    color: gray;
}

