
.app {
    display: grid;
    place-items: center;
    background: linear-gradient(180deg, #009688 20%, #DADBD3 20%);
    height: 100vh;
}

.app__body {
    display: flex;
    background-color: #EDEDED;
    height: 95vh;
    width: 90vw;
    box-shadow: -1px 4px 20px -6px rgba(0, 0, 0, 0.5);
}
